.container {
    padding: 0 10px;
}
.holder {
    position: relative;
    margin: 5px;
    background: #fff;
    border-radius: 20px;
    padding: 10px 20px;
    overflow: hidden;
    animation: showProduct ease 0.7s;
}
.name {
    color: #222;
    font-weight: bold;
    text-transform: uppercase;
    max-width: 200px;
}
.top, .bottom {
    display: flex;
    justify-content: space-between;
}
.bottom {
    align-items: end;
}
.price {
    font-weight: bold;
}
.info {
    font-size: 14px;
    color: #999999;
}
.info > div > span {
    position: relative;
}
.weight, .price {
    display: block;
    text-align: right;
    width: 100px;
}
.weight {
    color: #a7a7a7;
    font-weight: bold;
}
p {
    display: block;
    font-size: 20px;
    font-weight: bold;
    text-align: center;
    color: #fff;
}
img {
    max-width: 100%;
}

@keyframes showProduct {
    0% {
        transform: scaleY(0);
    }
    100% {
        transform: scaleY(1);
    }
}