.nav {
    margin: 10px 0 10px 10px;
    position: relative;
    display: flex;
    justify-content: center;
    font-family: Nishtiachok;
    font-size: 30px;
    letter-spacing: 0.1em;
    text-align: center;
    text-transform: uppercase;
    color: #FFC700;
}
.back {
    position: absolute;
    width: 0;
    height: 0;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
}

.back::after {
    position: relative;
    display: block;
    content: '<';
    font-size: 70px;
    transform: translateY(-50%);
    animation: show ease 0.7s;
}
.link {
    width: 0px;
    height: 0px;
}
.title {
    max-width: 80%;
    animation: showTitle ease 0.5s;
}

@keyframes show {
    0% {
        font-size: 0px;
    }
    100% {
        font-size: 70px;
    }
}
@keyframes showTitle {
    0% {
        transform: scale(0);
    }
    100% {
        transform: scale(1);
    }
}