.holder {
    position: relative;
    margin: 5px;
    background: #fff;
    border-radius: 20px;
    overflow: hidden;
    animation: show ease 0.7s;
}
.photo {
    display: flex;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;
    width: 100%;
    height: 120px;
    overflow: hidden;
}
.photo > img {
    max-width: 100%;
    max-height: 100%;
}
.name {
    margin-bottom: 15px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.name > span {
    color: #222;
    font-size: 14px;
    font-weight: bold;
    text-transform: uppercase;
    text-align: center;
    max-width: 90%;
}

@keyframes show {
    0% {
        transform: scale(0);
    }
    100% {
        transform: scale(1);
    }
}