.footer {
    padding: 30px 20px;
    text-align: center;
    display: flex;
    flex-direction: column;
    font-weight: bold;
    color: #4e3b29;
}
.location {
    color: #FFC700;
    margin-bottom: 15px;
    text-shadow: 1px 0 1px #000,
        0 1px 1px #000,
        -1px 0 1px #000,
        0 -1px 1px #000;
}
.contact_links {
    margin-bottom: 10px;
}
.contact_item {
    display: inline-block;
}
.contact_item > img {
    width: 40px;
    margin: 0 5px;
}
.developer {
    color: #FFC700;
}
.developer > a {
    color: #8400ff;
    text-decoration: underline;
}