.menu {
    padding: 0 10px;
}
.nav {
    margin: 10px 0 10px 10px;
    position: relative;
    display: flex;
    justify-content: center;
    font-family: Nishtiachok;
    font-size: 30px;
    letter-spacing: 0.1em;
    text-align: center;
    text-transform: uppercase;
    color: #FFC700;
}
.main {
    display: grid;
    grid-template-columns: 1fr 1fr;
}
.title {
    animation: showTitle ease 0.5s;
}
@keyframes showTitle {
    0% {
        transform: scale(0);
    }
    100% {
        transform: scale(1);
    }
}